function createAndJoinTable(lobby, params, router) {
  return function (dispatch) {
    lobby.push('create_table', params).receive('ok', function (resp) {
      joinTable(dispatch, resp.data.id, params.user, router);
    }).receive('error', function (resp) {
      console.log('error: could not create table');
      // TODO put error flash
    });
  };
}

function joinExisting(params, router) {
  return function (dispatch) {
    joinTable(dispatch, params.joinCode, params.user, router);
  };
}

function joinTable(dispatch, id) {
  var user = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : null;
  var router = arguments[3];

  var socket = new _phoenix.Socket('/socket', {});
  socket.connect();
  var channel = socket.channel('table:' + id, { user: user });
  channel.join().receive('ok', function (resp) {
    dispatch({ type: 'TABLE_JOIN', table: resp.data, channel: channel });
    router.history.push('/table');
  }).receive('error', function (resp) {
    console.log('error', resp);
    // TODO put error flash
  });

  setTableActions(dispatch, channel);
}

