function issueChallenge(channel, payload, router, user) {
  return function (dispatch) {
    channel.push('issue_challenge', payload).receive('ok', function (resp) {
      return console.log('challenge sent', resp);
    }).receive('error', function (resp) {
      return console.log('challenge failed', resp);
    });

    channel.on('challenge_accepted', function (msg) {
      if (msg.from_id == user.id) {
        joinGameChannel(dispatch, { id: user.id, params: payload }, user.id);
        router.history.push('/game');
      }
    });
  };
}

function acceptChallenge(channel, payload, router, user) {
  return function (dispatch) {
    channel.push('accept_challenge', payload).receive('ok', function (resp) {
      dispatch({ type: 'ACCEPTED_CHALLENGE', payload: payload });
      joinGameChannel(dispatch, { id: user.id, params: payload }, user.id);
      router.history.push('/game');
    }).receive('error', function (resp) {
      return console.log('error pushing accept', resp);
    });
  };
}

